import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ToastController, IonRouterOutlet, NavController, LoadingController, AlertController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-changepassword',
  templateUrl: './changepassword.page.html',
  styleUrls: ['./changepassword.page.scss'],
  providers: [ApiService]
})
export class ChangepasswordPage implements OnInit {

  password = '';
  repassword = '';

  subscribe: any;

  type = 'password';

  constructor(
    public NavController: NavController,
    public toastController: ToastController,
    private apiService: ApiService,
    public loadingController: LoadingController,
    public alertController: AlertController,
    private router: Router,
  ) { }

  ngOnInit() {
    let netwotkInformation = localStorage.getItem('netwotkInformation');
    if (netwotkInformation == 'offline') {
      this.alertNeedInternet();
    }
  }

  async alertNeedInternet() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Warning !',
      message: 'Halaman ini membutuhkan koneksi internet!',
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Kembali',
          handler: () => {
            this.goBack();
          }
        }
      ]
    });

    await alert.present();
  }

  changeCk(event: any) {
    if (event.detail.checked) {
      this.type = 'text';
    } else {
      this.type = 'password';
    }

  }

  async alertToRelogin() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Password berhasil diubah',
      message: 'Login kembali menggunakan Password baru Anda!',
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Login Ulang',
          handler: () => {
            this.router.navigate(['front/']);
          }
        }
      ]
    });

    await alert.present();
  }

  async presentLoading() {
    const loading = await this.loadingController.create({
      cssClass: 'my-custom-class',
      message: 'menyimpan data...',
      mode: 'ios',
      duration: 2000
    });
    await loading.present();

    const { role, data } = await loading.onDidDismiss();
    // console.log('Loading dismissed!');
  }

  async presentToast(color, header, message) {
    const toast = await this.toastController.create({
      header: header, //"Warning",
      message: message, //"Mohon isi semua formulir",
      color: color, //"danger",
      mode: "ios",
      position: "middle",
      duration: 3000
    });
    toast.present();
  }

  savingData() {

    // alert('ji');

    if (this.password != '' && this.repassword != '') {

      if (this.password == this.repassword) {

        if (this.password.length >= 6) {

          this.presentLoading();

          let currentUser = localStorage.getItem('currentUser');
          let d = JSON.parse(currentUser);

          let params = {
            password: this.password
          };

          this.subscribe = this.apiService.patch('users/' + d.user.id, params).subscribe(
            data => {

              localStorage.removeItem('currentUser');
              localStorage.removeItem('geo_id');
              localStorage.removeItem('api_key');

              this.alertToRelogin();
            },
            error => {
              setTimeout(() => {
                this.presentToast('danger', 'Error', 'Data tidak tersimpan');
              }, 2000);
              // this.presentToast('danger', 'Gagal Update Data', 'Mohono Hubungi Pihak Terkait');
            }
          );

        } else {
          this.presentToast('warning', 'Warning', 'Password minimal 6 karakter');
        }

      } else {
        this.presentToast('warning', 'Warning', 'Password dan Re-Password tidak cocok');
      }

    } else {
      this.presentToast('warning', 'Warning', 'Semua form wajib diisi');
    }

  }

  goBack() {
    this.NavController.navigateBack(['/home/profile'], { replaceUrl: true });
  }

}
