import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Platform, IonRouterOutlet, NavController, LoadingController, AlertController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';
import { AuthenticationService } from 'src/app/_services/authentication.service';

import { AppVersion } from '@ionic-native/app-version/ngx';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
  providers: [AuthenticationService, ApiService]
})
export class ProfilePage implements OnInit {

  subscribe: any;
  name: any;
  email: any;
  phone: any;

  showAppVersion = '-';

  constructor(
    private authService: AuthenticationService,
    public NavController: NavController,
    private apiService: ApiService,
    public alertController: AlertController,
    private router: Router,
    private appVersion: AppVersion
  ) {

    this.appVersion.getVersionNumber().then((version) => {
      // console.log(version);
      this.showAppVersion = version;
    }).catch(err => {
      // console.log(err);
    });


  }

  ngOnInit() {
    this.getDetailUser();
  }

  // ionViewDidEnter() {
  //   this.getDetailUser();
  // }

  async alertNeedInternet() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Warning !',
      message: 'Halaman ini membutuhkan koneksi internet!',
      mode: "ios",
      backdropDismiss: true,
      buttons: [
        {
          text: 'Tutup',
          role: 'cancel',
          handler: () => {
            // this.goBack();
          }
        }
      ]
    });

    await alert.present();
  }

  getDetailUser() {

    let currentUser = localStorage.getItem('currentUser');

    if (currentUser != null && currentUser != 'null' && currentUser != '') {
      let d = JSON.parse(currentUser);
      let usr = d.user;
      this.name = usr.name;
      this.email = usr.email;
      this.phone = (usr.phone_number != '' && usr.phone_number != null && usr.phone_number != 'null') ? '+62 ' + usr.phone_number : '';

    } else {
      this.alertToRelogin();
    }

  }

  gotoHelp() {
    // 
    this.NavController.navigateForward(['/home/help', { page: 'profile' }]);
  }

  gotoPage(path: string) {
    this.NavController.navigateForward([path]);
  }

  goBack() {
    this.NavController.navigateBack(['/home'], { replaceUrl: true });
  }

  async logOutUser() {

    let netwotkInformation = localStorage.getItem('netwotkInformation');
    if (netwotkInformation == 'offline') {

      this.alertNeedInternet();

    } else {

      const alert = await this.alertController.create({
        cssClass: 'my-custom-class',
        header: 'Logout dari aplikasi ?',
        message: 'Anda akan di minta login kembali',
        mode: "ios",
        backdropDismiss: true,
        buttons: [
          {
            text: 'Tidak',
            role: 'cancel',
            cssClass: 'secondary',
            handler: (blah) => {
              // this.counter = 0;
            }
          }, {
            text: 'Ya',
            handler: () => {

              this.subscribe = this.authService.logoutUser().subscribe(
                async (data) => {

                }, error => {
                  localStorage.removeItem('currentUser');
                  localStorage.removeItem('geo_id');
                  localStorage.removeItem('api_key');
                  this.router.navigate(['front/']);
                }
              );


            }
          }
        ]
      });

      await alert.present();

    }


  }

  async openAppsVersion() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Versi Aplikasi',
      message: this.showAppVersion,
      mode: "ios",
      backdropDismiss: true,
      buttons: [
        {
          text: 'Tutup',
          role: 'cancel'
        }
      ]
    });

    await alert.present();
  }

  async alertToRelogin() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Warning !',
      message: 'Akun anda terdeteksi sedang digunakan diperangkat lain, mohon login kembali untuk mendapatkan akses Anda kembali',
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Login Ulang',
          handler: () => {

            localStorage.removeItem('currentUser');
            localStorage.removeItem('geo_id');
            localStorage.removeItem('api_key');
            this.router.navigate(['front/']);

          }
        }
      ]
    });

    await alert.present();
  }

}
