import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LandingPage } from './landing/landing.page';
import { HelpPage } from './help/help.page';
import { PrivacypolicyPage } from './privacypolicy/privacypolicy.page';
import { ProfilePage } from './profile/profile.page';
import { ChangepasswordPage } from './changepassword/changepassword.page';
import { TemporaryreportPage } from './temporaryreport/temporaryreport.page';

import { DashboardPage } from './dashboard/dashboard.page';

const routes: Routes = [
  { path: '', component: LandingPage },
  {
    path: 'media',
    loadChildren: () => import('./media/media.module').then(m => m.MediaPageModule)
  },
  {
    path: 'help',
    component: HelpPage
  },
  {
    path: 'privacypolicy',
    component: PrivacypolicyPage
  },
  {
    path: 'profile',
    component: ProfilePage
  },
  {
    path: 'changepassword',
    component: ChangepasswordPage
  },
  {
    path: 'temporaryreport',
    component: TemporaryreportPage
  },
  {
    path: 'dashboard',
    component: DashboardPage
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class HomePageRoutingModule { }
